import React, { Component } from 'react'

// Router
import { Link } from 'react-router-dom'

import {
  Collapse,
  Navbar,
  NavbarToggler,
  NavbarBrand,
  Nav,
  NavItem,
  NavLink,
  UncontrolledDropdown,
  DropdownToggle,
  DropdownMenu,
  DropdownItem,
} from 'reactstrap'

export default class NavBar extends Component {
  constructor(props) {
    super(props)

    this.toggle = this.toggle.bind(this)
    this.state = {
      isOpen: false,
    }
  }
  toggle() {
    this.setState({
      isOpen: !this.state.isOpen,
    })
  }
  render() {
    return (
      <section>
        <div className="container">
          <div>
            <Navbar color="#f2f4f7" light expand="md">
              <Link className="navbar-brand" to="/">
                <img
                  src={require('../../assets/images/logi.png')}
                  className="img-fluid"
                />
              </Link>
              <NavbarToggler onClick={this.toggle} />
              <Collapse isOpen={this.state.isOpen} navbar>
                <Nav className="ml-auto" navbar>
                  <NavItem className="nav-item active">
                    <Link className="nav-link" to="/">
                      Home
                    </Link>
                  </NavItem>
                  <NavItem className="nav-item">
                    <Link className="nav-link" to="/about-us">
                      About Us
                    </Link>
                  </NavItem>

                  <UncontrolledDropdown nav inNavbar>
                    <DropdownToggle nav caret>
                      Property
                    </DropdownToggle>
                    <DropdownMenu right>
                      <DropdownItem>
                        <Link className="dropdown-item" to="/all-property">
                          All Property
                        </Link>
                      </DropdownItem>
                      <DropdownItem>
                        <Link className="dropdown-item" to="/property/amarene">
                          Amarene
                        </Link>
                      </DropdownItem>
                      <DropdownItem>
                        <Link
                          className="dropdown-item"
                          to="/property/forest-ville"
                        >
                          Forest Ville
                        </Link>
                      </DropdownItem>
                      <DropdownItem>
                        <Link
                          className="dropdown-item"
                          to="/property/imperial-grande"
                        >
                          Imperial Grande
                        </Link>
                      </DropdownItem>
                      <DropdownItem>
                        <Link
                          className="dropdown-item"
                          to="/property/one-foresta"
                        >
                          One Foresta
                        </Link>
                      </DropdownItem>
                      <DropdownItem>
                        <Link
                          className="dropdown-item"
                          to="/property/queens-water-front-1"
                        >
                          Queens Water Front 1
                        </Link>
                      </DropdownItem>
                      <DropdownItem>
                        <Link
                          className="dropdown-item"
                          to="/property/queens-water-front-2"
                        >
                          Queens Water Front 2
                        </Link>
                      </DropdownItem>
                      <DropdownItem>
                        <Link
                          className="dropdown-item"
                          to="/property/santorini"
                        >
                          Santorini
                        </Link>
                      </DropdownItem>
                      <DropdownItem>
                        <Link
                          className="dropdown-item"
                          to="/property/summer-skye"
                        >
                          Summer Skye
                        </Link>
                      </DropdownItem>
                    </DropdownMenu>
                  </UncontrolledDropdown>
                  <NavItem className="nav-item">
                    <Link className="nav-link" to="/contact-us">
                      Contact Us
                    </Link>
                  </NavItem>
                </Nav>
              </Collapse>
            </Navbar>
          </div>
          {/*<nav className="navbar navbar-expand-md navbar-dark hidden-sm">
            <Link className="navbar-brand" to="/">
              <img
                src={require('../../assets/images/logi.png')}
                className="img-fluid"
              />
            </Link>

            <button
              className="navbar-toggler"
              type="button"
              data-toggle="collapse"
              data-target="#collapsibleNavbar"
            >
              <span className="navbar-toggler-icon" />
            </button>

            <div className="collapse navbar-collapse" id="collapsibleNavbar">
              <ul className="navbar-nav ml-auto mt-2 mt-lg-0">
                <li className="nav-item active">
                  <Link className="nav-link" to="/">
                    Home
                  </Link>
                </li>
                <li className="nav-item">
                  <Link className="nav-link" to="/about-us">
                    About Us
                  </Link>
                </li>
                <li className="nav-item  dropdown">
                  <Link
                    className="nav-link dropdown-toggle"
                    to="/property"
                    id="navbarDropdownMenuLink"
                    data-toggle="dropdown"
                    aria-haspopup="true"
                    aria-expanded="false"
                  >
                    Property
                  </Link>
                  <div
                    className="dropdown-menu"
                    aria-labelledby="navbarDropdownMenuLink"
                  >
                    <Link className="dropdown-item" to="/all-property">
                      All Property
                    </Link>
                    <Link className="dropdown-item" to="/property/amarene">
                      Amarene
                    </Link>
                    <Link className="dropdown-item" to="/property/forest-ville">
                      Forest Ville
                    </Link>
                    <Link
                      className="dropdown-item"
                      to="/property/imperial-grande"
                    >
                      Imperial Grande
                    </Link>
                    <Link className="dropdown-item" to="/property/one-foresta">
                      One Foresta
                    </Link>
                    <Link
                      className="dropdown-item"
                      to="/property/queens-water-front-1"
                    >
                      Queens Water Front 1
                    </Link>
                    <Link
                      className="dropdown-item"
                      to="/property/queens-water-front-2"
                    >
                      Queens Water Front 2
                    </Link>
                    <Link className="dropdown-item" to="/property/santorini">
                      Santorini
                    </Link>
                    <Link className="dropdown-item" to="/property/summer-skye">
                      Summer Skye
                    </Link>
                  </div>
                </li>
                <li className="nav-item">
                  <Link className="nav-link" to="/contact-us">
                    Contact Us
                  </Link>
                </li>
              </ul>
            </div>
          </nav>

          <div className="row">
            <div className="col-5 col-sm-4 visible-sm hidden-menu cls-logo-img">
              <Link to="/">
                <img
                  src={require('../../assets/images/logo.png')}
                  className="img-fluid"
                />
              </Link>
            </div>

            <div className="col-7 col-sm-8 visible-sm hidden-menu">
              <div id="sidebarCollapse" className="navbar-btn">
                <i className="fas fa-bars" />
              </div>
            </div>
          </div>*/}
        </div>
      </section>
    )
  }
}



